body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.cart-icon {
  width: 45px;
  height: 45px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer; }
  .cart-icon .shopping-icon {
    width: 24px;
    height: 24px; }
  .cart-icon .item-count {
    position: absolute;
    font-size: 10px;
    font-weight: bold;
    bottom: 12px; }

.cart-dropdown {
  position: absolute;
  width: 240px;
  height: 340px;
  display: flex;
  flex-direction: column;
  padding: 20px;
  border: 1px solid black;
  background-color: white;
  top: 90px;
  right: 40px;
  z-index: 5; }
  .cart-dropdown .empty-message {
    font-size: 18px;
    margin: 50px auto; }
  .cart-dropdown .cart-items {
    height: 240px;
    display: flex;
    flex-direction: column;
    overflow: scroll; }
  .cart-dropdown button {
    margin-top: auto; }

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 80px;
}

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form-control {
  padding: 10px;
  margin: 10px;
  width: 600px;
}

.logo {
  margin-bottom: 20px;
}

